    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #68b738;
    transform: translate(-50%, -50%); }
  .range__toggle:hover, .range__toggle:focus {
    outline: none;
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25); }
  .range__toggle:focus {
    outline: none;
